.custom-card .ant-card-head {
    padding: 0 10px !important;
}

.custom-card .ant-card-body {
    padding: 10px;
}

.custom-card-today .ant-card-head {
    background: linear-gradient(180deg, #FBFCFF 0%, #F7FCFE 51.48%, #F5F8FF 100%);

}

.custom-card-totalappointments .ant-card-head {
    background: linear-gradient(180deg, #FFFBFE 0%, #FEF7FE 51.48%, #F5F8FF 100%);
}

.custom-card-confirmedappointments .ant-card-head {
    background: linear-gradient(180deg, #FBFFFC 0%, #F8FEF7 51.48%, #F5FFF5 100%);
}

.custom-card-notconfirmedappointments .ant-card-head {
    background: linear-gradient(180deg, #FFFBFB 0%, #FEF7F8 51.48%, #FFF5F5 100%);
}


.custom-card-showuprate .ant-card-head {
    background: linear-gradient(180deg, #FFFEFB 0%, #FEFBF7 51.48%, #FFFDF5 100%);
}

